import React, {Component} from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../../../../../store/actions'
import { withStyles } from '@material-ui/core/styles'
import {getRandomNumber} from '../../../../../util/random'
import {circleAround} from '../../../../../util/gsap'

import yellow from '@material-ui/core/colors/yellow'

import ButtleContent from './Buttle/ButtleContent'

const styles = () => ({
  root: {
    position: 'fixed'
  },
  static: {
    margin: '2rem'
  }
})

class BossContent extends Component {
  constructor () {
    super()
    this.state = {
      isDetect: false,
      canShootUser: true
    }
  }

  componentWillUnmount () {
    this.canceled = true
  }

  componentDidMount () {
    const {cubeRange, addNewEnemyButtle, isStatic} = this.props
    const boss = this.refs.boss
    this.createBoss(boss)
    if (isStatic) return

    const centerPoint = {
      x: cubeRange.x / 2,
      y: cubeRange.y / 2
    }

    const animation = circleAround({
      centerPoint,
      target: boss,
      duration: 10,
      continued: () => {
        const {canShootUser} = this.state
        this.checkPosition(this.refs.boss, centerPoint, animation)
        if (canShootUser) {
          !this.canceled && this.setState({canShootUser: false})
          setTimeout(() => {
            this.refs.boss && addNewEnemyButtle({
              timestamp: Date.now(),
              cubeRange,
              bossPosition: this.refs.boss.getBoundingClientRect(),
              centerPoint
            })
            !this.canceled && this.setState({canShootUser: true})
          }, getRandomNumber(5) * 1000)
        }
      }
    })
  }

  checkPosition = (element, centerPoint, animation) => {
    const {isDetect} = this.state
    const {enemy, updateEnemy} = this.props

    if (isDetect || this.canceled) return

    const position = element.getBoundingClientRect()

    updateEnemy('boss', {
      ...enemy,
      animation,
      currentPosition: {
        x: position.x,
        y: position.y
      }
    })

    !this.canceled && this.setState({isDetect: true})
    setTimeout(() => !this.canceled && this.setState({isDetect: false}), 100)
  }

  createBoss = (boss) => {
    const newBoss = boss.getContext('2d')

    newBoss.beginPath()
    newBoss.arc(30, 30, 30, 0, 2 * Math.PI)
    newBoss.fillStyle = yellow['A700']
    newBoss.fill()
    newBoss.beginPath()
    newBoss.arc(30, 45, 5, 0, 2 * Math.PI)
    newBoss.fillStyle = yellow['A100']
    newBoss.fill()
    newBoss.beginPath()
    newBoss.arc(15, 20, 5, 0, 2 * Math.PI)
    newBoss.fillStyle = yellow['A100']
    newBoss.fill()
    newBoss.beginPath()
    newBoss.arc(45, 20, 5, 0, 2 * Math.PI)
    newBoss.fillStyle = yellow['A100']
    newBoss.fill()
    newBoss.moveTo(30, 45)
    newBoss.lineTo(30, 30)
    newBoss.lineTo(15, 20)
    newBoss.moveTo(30, 30)
    newBoss.lineTo(45, 20)
    newBoss.strokeStyle = yellow['A100']
    newBoss.lineWidth = 2
    newBoss.stroke()
  }

  render () {
    const {classes, enemy = {life: 3}, cubeRange = {x: 0}, enemyButtleList, isStatic} = this.props

    return (
      <div>
        <canvas
          ref='boss'
          width={60}
          height={60}
          className={isStatic ? classes.static : classes.root}
          style={{
            left: `${cubeRange.x / 2 - 30}px`,
            top: '0',
            width: '60px',
            height: '60px',
            opacity: `${enemy.life * 0.333}`,
            borderRadius: '50%',
            boxShadow: `0 0 1rem ${yellow[100]}`
          }} />
        {
          enemyButtleList.map(buttle => <ButtleContent key={buttle.timestamp} {...buttle} />)
        }
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    enemyButtleList: state.enemyButtleList
  }
}

const mapDispatchToProps = dispatch => {
  return {
    updateEnemy: (key, enemy) => dispatch(actionCreators.updateEnemy(key, enemy)),
    toggleHitUser: (isHitUser) => dispatch(actionCreators.toggleHitUser(isHitUser)),
    addNewEnemyButtle: (buttle) => dispatch(actionCreators.addNewEnemyButtle(buttle))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(BossContent))



