import React, {Component} from 'react'
import ReactDOM from 'react-dom'
import { connect } from 'react-redux'
import * as actionCreators from '../../../../../../store/actions'
import { withStyles } from '@material-ui/core/styles'

import yellow from '@material-ui/core/colors/yellow'

import {attactUser, killAnimation} from '../../../../../../util/gsap'

const styles = () => ({
  root: {}
})

class ButtleContent extends Component {
  constructor () {
    super()
    this.state = {}
  }

  componentDidMount () {
    const {timestamp, bossPosition, cubeRange, deleteEnemyButtle} = this.props
    const buttle = ReactDOM.findDOMNode(this)

    this.createButtle(buttle)

    const animation = attactUser({
      target: buttle,
      appear: {
        x: bossPosition.x,
        y: bossPosition.y
      },
      range: cubeRange,
      callback: () => deleteEnemyButtle({timestamp}),
      continued: () => this.checkHitUser(animation, buttle, {timestamp})
    })
  }

  checkHitUser = (animation, element, buttleKey) => {
    const {centerPoint, toggleHitUser, deleteEnemyButtle, shield} = this.props
    const buttlePosition = element.getBoundingClientRect()

    const distance = Math.sqrt(
      Math.pow((centerPoint.x - buttlePosition.x), 2) +
      Math.pow((centerPoint.y - buttlePosition.y), 2)
    )

    // console.log(buttlePosition.x, shield)

    if (
      buttlePosition.x >= shield.left &&
      buttlePosition.x <= shield.right &&
      buttlePosition.y >= shield.top &&
      buttlePosition.y <= shield.bottom
    ) {
      killAnimation({animation})
      deleteEnemyButtle(buttleKey)
    } else {
      if (distance < 80) {
        killAnimation({animation})
        deleteEnemyButtle(buttleKey)
        toggleHitUser(true, 20)
        setTimeout(() => toggleHitUser(false), 150)
      }
    }
  }

  createButtle = (buttle) => {
    const newButtle = buttle.getContext('2d')

    newButtle.beginPath()
    newButtle.arc(10, 10, 10, 0, 2 * Math.PI)
    newButtle.fillStyle = yellow['A700']
    newButtle.fill()
  }

  render () {
    const {bossPosition} = this.props

    return (
      <canvas width={20} height={20} style={{
        position: 'fixed',
        left: `${bossPosition.left - 10}px`,
        top: `${bossPosition.top - 10}px`,
        width: '20px',
        height: '20px',
        borderRadius: '50%',
        boxShadow: `0 0 1rem ${yellow[100]}`
      }} />
    )
  }
}

const mapStateToProps = state => {
  return {
    shield: state.shield
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleHitUser: (isHitUser) => dispatch(actionCreators.toggleHitUser(isHitUser)),
    deleteEnemyButtle: (buttle) => dispatch(actionCreators.deleteEnemyButtle(buttle))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(ButtleContent))



