import React, {Component} from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../../../../store/actions'
import { withStyles } from '@material-ui/core/styles'

import ButtleContent from './Buttle/ButtleContent'

const styles = () => ({
  root: {
    position: 'relative',
    display: 'inline-block',
    width: 120,
    height: 120,
    transformOrigin: '60px 60px',
    transition: 'all 0.1s'
  },
  centerArea: {
    position: 'fixed',
    top: 'calc(50vh - 60px)',
    left: 'calc(50vw - 60px)',
    width: 120,
    height: 120
  },
  staticArea: {
    width: 120,
    height: 120,
    transform: 'scale(.5)',
    display: 'inline-block'
  },
  arrow: {
    position: 'absolute',
    top: '-40px',
    left: '50px'
  },
  core: {
    border: '10px solid #fff',
    borderRadius: '50%',
    boxShadow: '0 0 1rem #fff',
    transition: 'all 0.05s ease'
  },
  hurt: {
    boxShadow: '0 0 1.5rem darkred'
  },
  shield: {
    position: 'absolute',
    top: '100px',
    left: '-15px'
  }
})

class UserContent extends Component {
  constructor () {
    super()
    this.state = {
      rotate: 360,
      centerPoint: {
        x: document.documentElement.clientWidth / 2,
        y: document.documentElement.clientHeight / 2
      },
      canShoot: true
    }
  }

  componentDidMount () {
    this.createArrow()
    this.createCore()
    this.createShield()

    if (this.props.isStatic) return
    this.updateShieldPosition()
    document.addEventListener('keydown', this.controlDirection, false)
  }

  componentWillUnmount () {
    this.isCanceled = true
    document.removeEventListener('keydown', this.controlDirection, false)
  }

  createArrow = () => {
    const arrow = this.refs.arrow.getContext('2d')

    arrow.moveTo(10, 0)
    arrow.lineTo(20, 20)
    arrow.lineTo(0, 20)
    arrow.lineTo(10, 0)
    arrow.fillStyle = '#fff'
    arrow.fill()
  }

  createCore = () => {
    const core = this.refs.core.getContext('2d')

    core.moveTo(50, 50)
    core.lineTo(50, 0)
    core.moveTo(50, 50)
    core.lineTo(0, 100)
    core.moveTo(50, 50)
    core.lineTo(100, 100)
    core.strokeStyle = '#fff'
    core.lineWidth = 2
    core.stroke()
  }

  createShield = () => {
    const shield = this.refs.shield.getContext('2d')

    shield.beginPath()
    shield.arc(75, -40, 75, 0.5 * Math.PI, -1.5 * Math.PI)
    shield.strokeStyle = '#fff'
    shield.lineWidth = 5
    shield.stroke()
  }

  controlDirection = (event) => {
    const {rotate, centerPoint, canShoot} = this.state
    const {code} = event

    if (code === 'ArrowLeft') {
      this.setState({rotate: rotate - 15})
      this.updateShieldPosition()
    }

    if (code === 'ArrowRight') {
      this.setState({rotate: rotate + 15})
      this.updateShieldPosition()
    }

    if (code === 'Space' && canShoot) {
      const {addNewButtle} = this.props
      const arrow = this.refs.arrow
      const arrowPosition = arrow.getBoundingClientRect()
      const {left, right, top, bottom, x, y} = arrowPosition
      const startPoint = {
        x: x + (right - left) / 2,
        y: y + (bottom - top) / 2
      }
      const limitPoint = this.getLimitPoint({
        centerPoint,
        startPoint,
        rotate
      })

      !this.isCanceled && this.setState({canShoot: false})
      setTimeout(() => !this.isCanceled && this.setState({canShoot: true}), 400)

      addNewButtle({
        rotate: this.state.rotate,
        x: startPoint.x,
        y: startPoint.y,
        timestamp: Date.now(),
        limit: {
          x: limitPoint.x - centerPoint.x,
          y: limitPoint.y - centerPoint.y
        }
      })
    }
  }

  updateShieldPosition = () => {
    const {updateShield} = this.props
    const shield = this.refs.shield
    const shieldPosition = shield.getBoundingClientRect()
    const {top, left, bottom, right} = shieldPosition
    updateShield({top, left, bottom, right})
  }

  getLimitPoint ({startPoint, centerPoint, rotate}) {
    const slope = ((startPoint.y - centerPoint.y) / (startPoint.x - centerPoint.x))
    const currentRotate = (rotate < 0) ? rotate * -1 % 360 : rotate % 360
    let limitPoint = {}

    if (currentRotate % 45 === 0) {
      switch (currentRotate) {
        case 45:
          limitPoint = {x: centerPoint.x * 2, y: 0}
          break
        case 90:
          limitPoint = {x: centerPoint.x * 2, y: centerPoint.y}
          break
        case 135:
          limitPoint = {x: centerPoint.x * 2, y: centerPoint.y * 2}
          break
        case 180:
          limitPoint = {x: centerPoint.x, y: centerPoint.y * 2}
          break
        case 225:
          limitPoint = {x: 0, y: centerPoint.y * 2}
          break
        case 270:
          limitPoint = {x: 0, y: centerPoint.y}
          break
        case 315:
          limitPoint = {x: 0, y: 0}
          break
        case 0:
        default:
          limitPoint = {x: centerPoint.x, y: 0}
          break
      }
    } else {
      if ((currentRotate > 0 && currentRotate <= 45) || (currentRotate > 315 && currentRotate <= 360)) {
        limitPoint = {y: 0, flag: 'y'}
      } else if (currentRotate > 45 && currentRotate <= 135) {
        limitPoint = {x: centerPoint.x * 2, flag: 'x'}
      } else if (currentRotate > 135 && currentRotate <= 225) {
        limitPoint = {y: centerPoint.y * 2, flag: 'y'}
      } else if (currentRotate > 225 && currentRotate <= 315) {
        limitPoint = {x: 0, flag: 'x'}
      }
    }

    // m = (y2 - y1) / (x2 - x1)
    // m * (x2 - x1) = (y2 - y1)
    // x2 = (y2 - y1) / m + x1

    // m(x2 - x1) = (y2 - y1)
    // m(x2 - x1) + y1

    switch (limitPoint.flag) {
      case 'x':
        return {
          x: limitPoint.x,
          y: slope * (limitPoint.x - centerPoint.x) + centerPoint.y
        }
      case 'y':
        return {
          x: (limitPoint.y - centerPoint.y) / slope + centerPoint.x,
          y: limitPoint.y
        }
      default:
        return {
          x: limitPoint.x,
          y: limitPoint.y
        }
    }
  }

  render () {
    const {
      classes,
      buttleList,
      isHitUser,
      isStatic = false
    } = this.props
    const {rotate, centerPoint} = this.state

    return (
      <div ref='root' className={isStatic ? classes.staticArea : classes.centerArea}>
        <div
          className={classes.root}
          onKeyPress={this.controlDirection}
          style={{
            transform: `rotate(${rotate}deg)`
          }}>
          <canvas className={classes.arrow} ref='arrow' width={20} height={20} />
          <canvas
            className={`${classes.core} ${isHitUser ? classes.hurt : ''}`}
            ref='core'
            width={100}
            height={100} />
          <canvas className={classes.shield} ref='shield' width={150} height={50} />
        </div>
        {
          buttleList.map((buttle) => (
            <ButtleContent
              key={buttle.timestamp}
              centerPoint={centerPoint}
              {...buttle} />
          ))
        }
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    buttleList: state.buttleList,
    isHitUser: state.isHitUser
  }
}

const mapDispatchToProps = dispatch => {
  return {
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded)),
    addNewButtle: (buttle) => dispatch(actionCreators.addNewButtle(buttle)),
    updateShield: (shield) => dispatch(actionCreators.updateShield(shield))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(UserContent))



