import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import ReadmeContent from '../component/Readme/ReadmeContent'

class Readme extends Component {
  render () {
    return (
      <main>
        <ReadmeContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    player: state.player,
    isModalOpen: state.isModalOpen
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleModal: () => dispatch(actionCreators.toggleModal()),
    updatePlayer: (player) => dispatch(actionCreators.updatePlayer(player))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Readme)



