import React, {Component} from 'react'
import ReactDOM from 'react-dom'
import { connect } from 'react-redux'
import * as actionCreators from '../../../../../store/actions'
import { withStyles } from '@material-ui/core/styles'
import * as CONSTANT from '../../../../../constant'

import blue from '@material-ui/core/colors/blue'

import {attactUser, killAnimation} from '../../../../../util/gsap'

const styles = () => ({
  root: {
    position: 'fixed'
  },
  static: {
    margin: '2rem'
  }
})

class AngleContent extends Component {
  constructor () {
    super()
    this.state = {
      isDetect: false
    }
  }

  componentWillUnmount () {
    this.canceled = true
  }

  componentDidMount () {
    const {enemy, cubeRange, isStatic} = this.props
    const angle = ReactDOM.findDOMNode(this)
    this.createAngle(angle)
    if (isStatic) return

    const centerPoint = {
      x: cubeRange.x / 2,
      y: cubeRange.y / 2
    }

    const animation = attactUser({
      appear: {x: enemy.x, y: enemy.y},
      range: {...cubeRange},
      target: angle,
      duration: 5,
      continued: () => !this.canceled && this.checkPosition(ReactDOM.findDOMNode(this), centerPoint, animation)
    })
  }

  checkPosition = (element, centerPoint, animation) => {
    const {isDetect} = this.state
    const {enemy, updateEnemy} = this.props

    if (isDetect || this.canceled) return

    const {deleteEnemy} = this.props
    const position = element.getBoundingClientRect()
    let newCenterPoint = {}

    updateEnemy('angle', {
      ...enemy,
      animation,
      currentPosition: {
        x: position.x,
        y: position.y
      }
    })

    switch (enemy.appear) {
      case CONSTANT.APPEAR_POSITION.TOP:
        newCenterPoint = {x: centerPoint.x, y: centerPoint.y}
        break
      case CONSTANT.APPEAR_POSITION.LEFT:
        newCenterPoint = {x: centerPoint.x, y: centerPoint.y}
        break
      case CONSTANT.APPEAR_POSITION.BOTTOM:
        newCenterPoint = {x: centerPoint.x, y: centerPoint.y - 60}
        break
      case CONSTANT.APPEAR_POSITION.RIGHT:
        newCenterPoint = {x: centerPoint.x - 60, y: centerPoint.y}
        break
      default:
        break
    }

    const distance = Math.sqrt(
      Math.pow((newCenterPoint.x - position.x), 2) +
      Math.pow((newCenterPoint.y - position.y), 2)
    )

    if (distance <= 120) {
      const {toggleHitUser} = this.props

      toggleHitUser(true)
      killAnimation({animation})
      deleteEnemy('angle', {timestamp: enemy.timestamp})
      setTimeout(() => toggleHitUser(false), 100)
    }

    !this.canceled && this.setState({isDetect: true})
    setTimeout(() => !this.canceled && this.setState({isDetect: false}), 100)
  }

  createAngle = (angle) => {
    const newAngle = angle.getContext('2d')

    newAngle.moveTo(30, 0)
    newAngle.lineTo(0, 60)
    newAngle.lineTo(60, 60)
    newAngle.lineTo(30, 0)
    newAngle.fillStyle = blue[500]
    newAngle.fill()
    newAngle.beginPath()
    newAngle.arc(30, 40, 10, 0, 2 * Math.PI)
    newAngle.fillStyle = blue[100]
    newAngle.fill()
  }

  render () {
    const {enemy = {x: 0, y: 0}, classes, isStatic} = this.props

    return (
      <canvas
        width={60}
        height={60}
        className={isStatic ? classes.static : classes.root}
        style={{
          left: `${enemy.x - 10}px`,
          top: `${enemy.y - 10}px`,
          width: '60px',
          height: '60px'
        }} />
    )
  }
}

const mapStateToProps = state => {
  return {
    buttleList: state.buttleList
  }
}

const mapDispatchToProps = dispatch => {
  return {
    updateEnemy: (key, enemy) => dispatch(actionCreators.updateEnemy(key, enemy)),
    toggleHitUser: (isHitUser) => dispatch(actionCreators.toggleHitUser(isHitUser))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(AngleContent))



