import {TweenLite, TweenMax, Linear} from 'gsap'
import 'gsap/CSSPlugin'

export const shootButtle = ({target, duration = 2, animation, continued, callback}) => {
  return TweenLite.to(target, duration, {
    ...animation,
    onComplete: () => {
      callback && callback()
    },
    onUpdate: () => {
      continued && continued()
    }
  })
}

export const attactUser = ({target, duration = 2, appear = {}, range = {}, continued, callback}) => {
  return TweenLite.to(target, duration, {
    x: (range.x / 2 - 20) - appear.x,
    y: (range.y / 2 - 20) - appear.y,
    onComplete: () => {
      callback && callback()
    },
    onUpdate: () => {
      continued && continued()
    }
  })
}

export const killAnimation = ({animation}) => animation.kill()

export const circleAround = ({centerPoint, target, duration, callback, continued}) => {
  const distance = centerPoint.y - 30
  return TweenMax.to(target, duration, {
    rotation: 360,
    transformOrigin: 'center',
    bezier: {
      curviness: 1.5,
      values: [
        {x: distance, y: distance},
        {x: 0, y: distance * 2},
        {x: -distance, y: distance},
        {x: 0, y: 0}
      ]
    },
    ease: Linear.easeNone,
    repeat: -1,
    onComplete: () => {
      callback && callback()
    },
    onUpdate: () => {
      continued && continued()
    }
  })
}



