import React, {Component} from 'react'
import ReactDOM from 'react-dom'
import { connect } from 'react-redux'
import * as actionCreators from '../../../../../store/actions'
import { withStyles } from '@material-ui/core/styles'

import {shootButtle, killAnimation} from '../../../../../util/gsap'

const styles = () => ({
  root: {}
})

class ButtleContent extends Component {
  constructor () {
    super()
    this.state = {}
  }

  componentDidMount () {
    const {limit, timestamp, deleteButtle} = this.props
    const buttle = ReactDOM.findDOMNode(this)

    this.createButtle(buttle)

    const animation = shootButtle({
      target: buttle,
      animation: {...limit},
      callback: () => deleteButtle({timestamp}),
      continued: () => this.checkHitEnemy(animation, buttle, {timestamp})
    })
  }

  createButtle = (buttle) => {
    const newButtle = buttle.getContext('2d')

    newButtle.beginPath()
    newButtle.arc(10, 10, 10, 0, 2 * Math.PI)
    newButtle.fillStyle = '#fff'
    newButtle.fill()
  }

  checkHitEnemy = (animation, element, buttleKey) => {
    const {enemys, deleteEnemy, deleteButtle, updateEnemy} = this.props
    const buttlePosition = element.getBoundingClientRect()

    Object.keys(enemys).forEach(enemyKey => {
      const enemyList = enemys[enemyKey]
      enemyList.forEach(enemy => {
        const {currentPosition} = enemy
        const distance = Math.sqrt(
          Math.pow((currentPosition.x - buttlePosition.x), 2) +
          Math.pow((currentPosition.y - buttlePosition.y), 2)
        )

        if (distance < 40) {
          killAnimation({animation})
          deleteButtle(buttleKey)
          if (enemy.life === 1) {
            killAnimation({animation: enemy.animation})
            deleteEnemy(enemyKey, {timestamp: enemy.timestamp}, true)
          } else {
            updateEnemy(enemyKey, {
              ...enemy,
              life: enemy.life - 1
            })
          }
        }
      })
    })
  }

  render () {
    const {timestamp, x, y, rotate} = this.props

    return (
      <canvas width={20} height={20} key={timestamp} style={{
        position: 'fixed',
        left: `${x - 10}px`,
        top: `${y - 10}px`,
        transform: `rotate(${rotate}deg)`,
        transformOrigin: 'center',
        width: '20px',
        height: '20px',
        borderRadius: '50%',
        boxShadow: '0 0 1rem #fff'
      }} />
    )
  }
}

const mapStateToProps = state => {
  return {
    enemys: state.enemys
  }
}

const mapDispatchToProps = dispatch => {
  return {
    deleteButtle: (buttle) => dispatch(actionCreators.deleteButtle(buttle)),
    deleteEnemy: (key, enemy, byUser) => dispatch(actionCreators.deleteEnemy(key, enemy, byUser)),
    updateEnemy: (key, enemy) => dispatch(actionCreators.updateEnemy(key, enemy))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(ButtleContent))



